import { Component, OnInit } from "@angular/core";

import { ExamService } from "../services/exam.services";
import { Router, ActivatedRoute } from "@angular/router";
import swal from "sweetalert2";
import { Exam } from "app/shared/models/exam.model";

@Component({
  selector: "app-edit-exam",
  templateUrl: "./edit-exam.component.html",
  styleUrls: ["./edit-exam.component.scss"]
})
export class EditExamComponent implements OnInit {
  updatedExam: Exam;
  examid;

  constructor(
    public examervice: ExamService,
    public router: Router,
    public activeRouter: ActivatedRoute
  ) {
    this.updatedExam = new Exam({});

    this.examid = this.activeRouter.snapshot.params["id"];
  }

  ngOnInit() {
    this.examervice.getExamById(this.examid).subscribe(
      (data: any) => {
        console.log("examlist>>", data);
        this.updatedExam = data.data;
      },
      error => {
        console.log("cant fetch");
      }
    );
  }

  update() {
    console.log("Submitted>>>>>>>>>", this.updatedExam);

    swal
      .fire({
        title: "Do you want to edit the exam details ?",
        text: "You won't be able to revert the change",
        icon: "warning",
        showCancelButton: true,
        confirmButtonColor: "#3085d6",
        cancelButtonColor: "#d33",
        confirmButtonText: "Confirm"
      })
      .then(result => {
        if (result.value) {
          //dev

          this.examervice.editExam(this.updatedExam).subscribe(
            (data: any) => {
              swal.fire("Edit!", "Exam has been edited.", "success");
              //dev
              console.log("Dataaa changeddd", data);
              this.ngOnInit();
            },
            error => {
              swal.fire("Not Changed!", "There was some error.", "error");
              //dev
              console.log("ERRORR", error);
            }
          );
        }
      });
  }
}
