import { Component, OnInit } from "@angular/core";
import { ExamService } from "../services/exam.services";
import { Router, ActivatedRoute } from "@angular/router";

import swal from "sweetalert2";
import { Exam } from "app/shared/models/exam.model";

@Component({
  selector: "app-add-exam",
  templateUrl: "./add-exam.component.html",
  styleUrls: ["./add-exam.component.scss"]
})
export class AddExamComponent implements OnInit {
  newexam: Exam;
  submitting: boolean;

  constructor(
    public examervice: ExamService,
    public router: Router,
    public activeRouter: ActivatedRoute
  ) {
    this.newexam = new Exam({});
  }

  ngOnInit() {}

  submit() {
    console.log("Submitted>>>>>>>>>", this.newexam);

    swal
      .fire({
        title: "Do you want to add the exam ?",
        text: "New exam will be added in Exam List",
        icon: "warning",
        showCancelButton: true,
        confirmButtonColor: "#3085d6",
        cancelButtonColor: "#d33",
        confirmButtonText: "Add"
      })
      .then(result => {
        if (result.value) {
          //dev

          this.examervice.addExam(this.newexam).subscribe(
            (data: any) => {
              swal.fire("Add!", "Exam has been added.", "success");
              //dev
              console.log("Dataaa changeddd", data);
            },
            error => {
              swal.fire("Not Added!", "There was some error.", "error");
              //dev
              console.log("ERRORR", error);
            }
          );
        }
      });
  }
}
