import { Component, OnInit } from "@angular/core";
import { Router } from "@angular/router";
import { ExamService } from "./services/exam.services";
import swal from "sweetalert2";

@Component({
  selector: "app-exam",
  templateUrl: "./exam.component.html",
  styleUrls: ["./exam.component.scss"]
})
export class ExamComponent implements OnInit {
  examList;

  constructor(public examservice: ExamService, private router: Router) {
    this.examList = [];
  }

  ngOnInit() {
    this.examservice.getExam().subscribe(
      (data: any) => {
        console.log("examlist>>", data);
        this.examList = data.data;
      },
      error => {
        console.log("cant fetch");
      }
    );
  }

  edit(id) {
    console.log("exam id>>", id);
    this.router.navigate(["exam/edit/" + id]);
  }

  delete(id) {
    swal
      .fire({
        title: "Do you want to delete the exam ?",
        text: "You won't be able to revert the deleted exam",
        icon: "warning",
        showCancelButton: true,
        confirmButtonColor: "#3085d6",
        cancelButtonColor: "#d33",
        confirmButtonText: "Delete"
      })
      .then(result => {
        if (result.value) {
          //dev

          this.examservice.deleteExam(id).subscribe(
            (data: any) => {
              swal.fire("Deleted!", "Exam has been deleted.", "success");
              //dev
              console.log("Dataaa changeddd", data);
              this.ngOnInit();
            },
            error => {
              swal.fire("Not Changed!", "There was some error.", "error");
              //dev
              console.log("ERRORR", error);
            }
          );
        }
      });
  }
}
